import {
  Box,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import React, { useCallback, useMemo } from "react";
import { useState } from "react";
import { currencyFormatter } from "../util";
import { ERBarChar } from "./components/ERBarChart";
import { Item } from "./Item";

const toDisplay = (val) => (val && isNaN(Number(val)) ? val : Number(val).toFixed(0));
const increaseByPercent = (value, percent) => value + value * percent;
export function ERCalculator() {
  const returnYears = 20;
  const returnPercent = 5 / 100;
  const inventmentAmt = 10000;
  const [targetSplits, setTargetSplits] = useState([20, 30, 50]);
  const [ers, setERs] = useState([0.1, 0.06, 1.2]);
  const onSplitPercentChanges = useCallback(
    (id, setter) => (e) => {
      setter((oldValues) => {
        const newValues = [...oldValues];
        newValues[id] = e.target.value;
        return newValues;
      });
    },
    []
  );
  const totalEr = useMemo(
    () => targetSplits.reduce((acc, curr, i) => (acc += (curr / 100) * 1 * ers[i]), 0),
    [targetSplits, ers]
  );
  const returnTable = useMemo(() => {
    const totalErPecent = totalEr / 100;
    const returns = [[inventmentAmt, inventmentAmt]];
    new Array(returnYears).fill().forEach((_c, i) => {
      const returnV = increaseByPercent(returns[i][0], returnPercent);
      const returnAfyerCost = increaseByPercent(returns[i][1], returnPercent);
      returns.push([returnV, returnAfyerCost - returnAfyerCost * totalErPecent]);
    });
    return returns;
  }, [totalEr]);
  const chartData = useMemo(() => {
    const [v1, v2] = returnTable[returnYears];
    return [
      { title: "Return", value: v1 },
      { title: "After Fees", value: v2, fees: v1 - v2 },
    ];
  }, [returnTable]);
  const cagrAfterFee = useMemo(() => {
    const v2 = returnTable[returnYears][1];
    return Math.pow(v2 / inventmentAmt, 1 / returnYears) - 1;
  }, [returnTable]);
  return (
    <div className="App">
      <h1>ER Calculator</h1>
      <Box display="grid" gridTemplateColumns="repeat(12, 1fr)" gap={1}>
        {targetSplits.map((split, i) => {
          const er = ers[i];
          return (
            <Box gridColumn={`span ${12 / targetSplits.length}`} key={i}>
              <Item
                value={toDisplay(split)}
                onChange={onSplitPercentChanges(i, setTargetSplits)}
                label={`Target%-${i + 1}`}
                endAdornment={true}
                adornment="%"
              ></Item>
              <Item
                value={er}
                onChange={onSplitPercentChanges(i, setERs)}
                label={`ER-${i + 1}`}
                endAdornment={true}
                adornment="%"
              ></Item>
            </Box>
          );
        })}
      </Box>
      <Box padding={1}>
        <Typography variant="body1">Total ER:{totalEr.toFixed(2)}%</Typography>
        <Typography variant="subtitle2">
          If you invesnt {currencyFormatter.format(inventmentAmt)} for {returnYears} years, with yearly return of{" "}
          {returnPercent * 100}%,
        </Typography>

        <Typography>
          {((chartData[1].fees / chartData[0].value) * 100).toFixed(2)}% / {currencyFormatter.format(chartData[1].fees)}{" "}
          paid in fees, which is same as {(cagrAfterFee * 100).toFixed(2)}% return
        </Typography>
      </Box>
      <Box display="grid" gridTemplateColumns="repeat(2, 1fr)" gap={1}>
        <Box>
          <TableContainer component={Paper}>
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell>Year</TableCell>
                  <TableCell>Return</TableCell>
                  <TableCell>After Fees</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {returnTable
                  .filter((_v, i) => i > 0)
                  .map(([ret, retAfterFees], year) => {
                    return (
                      <TableRow key={year}>
                        <TableCell>{year + 1}</TableCell>
                        <TableCell>{currencyFormatter.format(ret)}</TableCell>
                        <TableCell>{currencyFormatter.format(retAfterFees)}</TableCell>
                      </TableRow>
                    );
                  })}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
        <Box>
          <ERBarChar data={chartData} />
        </Box>
      </Box>
    </div>
  );
}
