import PropTypes from "prop-types";
import { FormControl, InputAdornment, InputLabel, OutlinedInput } from "@mui/material";
import React from "react";

export const Item = ({
  value,
  onChange = null,
  label,
  endAdornment = false,
  adornment,
  error = false,
  disabled = false,
}) => {
  const id = Date.now();
  return (
    <div style={{ marginTop: 15 }}>
      <FormControl size="small">
        <InputLabel htmlFor={`${id}`}>{label}</InputLabel>
        <OutlinedInput
          size="small"
          id={`${id}`}
          margin="dense"
          value={value}
          onChange={onChange}
          startAdornment={!endAdornment ? <InputAdornment position="start">{adornment}</InputAdornment> : false}
          endAdornment={endAdornment ? <InputAdornment position="end">{adornment}</InputAdornment> : false}
          label={label}
          error={error}
          disabled={disabled}
        />
      </FormControl>
    </div>
  );
};

Item.propTypes = {
  adornment: PropTypes.any,
  disabled: PropTypes.bool,
  endAdornment: PropTypes.bool,
  error: PropTypes.bool,
  label: PropTypes.any,
  onChange: PropTypes.any,
  value: PropTypes.any,
};
