import PropTypes from "prop-types";
import React from "react";
import { ResponsiveContainer, BarChart, XAxis, YAxis, Tooltip, Legend, Bar } from "recharts";
import { currencyFormatter } from "../../util";

const CustomTooltip = ({ active, payload, label }) => {
  if (active && payload && payload.length) {
    const { value, fees } = payload[0].payload;
    return (
      <div className="custom-tooltip">
        <p className="label">{`${label} : ${currencyFormatter.format(value)}`}</p>
        {fees && <p className="intro">{`Fees : ${currencyFormatter.format(fees)}`}</p>}
      </div>
    );
  }
  return null;
};

CustomTooltip.propTypes = {
  active: PropTypes.bool,
  label: PropTypes.string,
  payload: PropTypes.shape({
    length: PropTypes.any,
  }),
};

const MyLabel = (props) => {
  console.log({ props });
  return (
    <text className="recharts-label" {...props.viewBox}>
      {props.value}
    </text>
  );
};

MyLabel.propTypes = {
  value: PropTypes.any,
  viewBox: PropTypes.any,
};
export const ERBarChar = ({ data }) => {
  return (
    <ResponsiveContainer>
      <BarChart
        data={data}
        margin={{
          top: 5,
          right: 30,
          left: 20,
          bottom: 5,
        }}
      >
        <XAxis dataKey="title" />
        <YAxis />
        <Tooltip content={<CustomTooltip active={undefined} payload={undefined} label={undefined} />} />
        <Legend />
        <Bar dataKey="value" stackId="a" fill="#82ca9d" />
        <Bar dataKey="fees" stackId="a" fill="#8884d8" />
      </BarChart>
    </ResponsiveContainer>
  );
};

ERBarChar.propTypes = {
  data: PropTypes.any,
};
