import React, { useCallback, useEffect, useState } from "react";
import IconButton from "@mui/material/IconButton";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputAdornment from "@mui/material/InputAdornment";
import TextField from "@mui/material/TextField";
import { green, red } from "@mui/material/colors";
import Icon from "@mui/material/Icon";
import "./styles.css";
import { Box, Paper, Typography } from "@mui/material";
import NumberFormatCustom from "./components/NumberFormatCustom";
import { Item } from "./Item";

export function RebalanceCalculator() {
  const [amount] = useState(10000);
  const [splitPrices, setSplitPrices] = useState([86.74, 982.47, 3.47]);
  const [targetSplits, setTargetSplits] = useState([20, 30, 50]);
  const [currentSplits, setCurrentSplits] = useState([2428, 3929, 6800]);
  const [totalPercent, setTotalPercent] = useState(0);
  const [currentTotal, setCurrentTotal] = useState(0);
  const [newInvestment, setNewInvestment] = useState(22800);
  const [rebelanceSummary, setRebalanceSummary] = useState([]);
  const [totalRebalanaceResidue, setTotalRebalanaceResidue] = useState("");
  const [costPerTx, setCostPerTx] = useState("0.1");
  useEffect(() => setTotalPercent(targetSplits.reduce((acc, c) => acc + Number(c), 0)), [targetSplits]);
  useEffect(() => setCurrentTotal(currentSplits.reduce((acc, c) => acc + Number(c), 0)), [currentSplits]);

  const onSplitPriceChanges = useCallback(
    (id) => (e) => {
      const { value } = e.target;
      const newSplitPrices = [...splitPrices];
      newSplitPrices[id] = value;
      setSplitPrices(newSplitPrices);
    },
    [splitPrices]
  );
  const onSplitPercentChanges = useCallback(
    (id) => (e) => {
      const newtargetSplits = [...targetSplits];
      newtargetSplits[id] = Number(e.target.value);
      setTargetSplits(newtargetSplits);
    },
    [targetSplits]
  );
  const onCurrentSplitValueChanges = useCallback(
    (id) => (e) => {
      const { value } = e.target;
      const newtargetSplits = [...currentSplits];
      newtargetSplits[id] = value;
      setCurrentSplits(newtargetSplits);
    },
    [currentSplits]
  );
  const handleAddtargetSplits = useCallback(() => {
    setTargetSplits((olstargetSplits) => [...olstargetSplits, 0]);
  }, []);

  const handleRemovetargetSplits = useCallback((idx) => {
    setTargetSplits((olstargetSplits) => {
      olstargetSplits.splice(idx, 1);
      return [...olstargetSplits];
    });
  }, []);
  useEffect(() => {
    let totalResidueAmt = 0;
    const rebalanceSummaryX = targetSplits.map((splitPercent, i) => {
      const splitPx = splitPrices[i];
      const targetAmountFromTotalCurrent = (currentTotal * splitPercent) / 100;
      const newInvestmentSplit = (newInvestment * targetSplits[i]) / 100;
      const currentSplit = currentSplits[i] ? Number(currentSplits[i]) : 0;
      const rebalanceAmt = targetAmountFromTotalCurrent - currentSplit + newInvestmentSplit;
      const rebalanceQty = Number(rebalanceAmt / splitPx);
      const side = rebalanceQty > 0 ? "Buy" : "Sell";
      const txTotal = Number(Number(Math.abs(Math.floor(rebalanceQty))) * splitPx).toFixed(2);
      totalResidueAmt += Number((rebalanceQty - Number(Math.floor(rebalanceQty))) * splitPx);
      return {
        side,
        rebalanceQty: Math.abs(Math.floor(rebalanceQty)),
        txTotal,
      };
    });
    setRebalanceSummary(rebalanceSummaryX);
    setTotalRebalanaceResidue(totalResidueAmt.toFixed(2));
  }, [targetSplits, splitPrices, currentSplits, currentTotal, newInvestment, costPerTx]);
  const handleNewInvestmentChange = useCallback((e) => setNewInvestment(Number(e.target.value)), []);
  return (
    <div className="App">
      <h1>Portfolio Rebalance Calculator</h1>
      <Box
        className="split-section"
        sx={{
          "& > :not(style)": {
            m: 1,
          },
        }}
      >
        <TextField
          label="Investment Amount"
          value={newInvestment}
          onChange={handleNewInvestmentChange}
          name="numberformat"
          id="newInvetment"
          InputProps={{
            inputComponent: NumberFormatCustom,
          }}
          variant="standard"
        />
        <TextField
          label="Cost per Transaction"
          value={costPerTx}
          onChange={(e) => setCostPerTx(e.target.value)}
          name="numberformat"
          InputProps={{
            inputComponent: NumberFormatCustom,
          }}
          variant="standard"
        />
      </Box>
      <Box
        className="split-section"
        sx={{
          "& > :not(style)": {
            m: 1,
          },
        }}
      >
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="total">Total</InputLabel>
          <OutlinedInput
            id="total"
            size="small"
            disabled
            value={totalPercent}
            endAdornment={<InputAdornment position="end">%</InputAdornment>}
            label="Total"
            error={totalPercent !== 100}
          />
        </FormControl>
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="Total(£)">Total(£)</InputLabel>
          <OutlinedInput
            size="small"
            id="Total(£)"
            label="Total(£)"
            disabled
            value={(amount * totalPercent) / 100}
            name="numberformat"
            inputComponent={NumberFormatCustom}
          />
        </FormControl>
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="balance">Current Total</InputLabel>
          <OutlinedInput
            size="small"
            id="balance"
            label="Current Total"
            disabled
            value={currentTotal}
            name="numberformat"
            inputComponent={NumberFormatCustom}
          />
        </FormControl>
      </Box>
      <Box display="grid" gridTemplateColumns="repeat(12, 1fr)" gap={1}>
        {targetSplits.map((split, i) => {
          const splitPx = splitPrices[i];
          const currentValue = currentSplits[i] ? Number(currentSplits[i]) : 0;
          const currentPercent = Number(currentSplits[i] ? Number((currentSplits[i] * 100) / currentTotal) : 0).toFixed(
            0
          );

          const targetAmountFromTotalCurrent = (currentTotal * split) / 100;
          const newInvestmentSplit = (newInvestment * targetSplits[i]) / 100;
          const currentSplit = currentSplits[i] ? Number(currentSplits[i]) : 0;
          const rebalanceAmt = targetAmountFromTotalCurrent - currentSplit + newInvestmentSplit;
          return (
            <Box gridColumn={`span ${12 / targetSplits.length}`} key={i}>
              <Item
                value={split.toFixed(0)}
                onChange={onSplitPercentChanges(i)}
                label={`Target%-${i + 1}`}
                endAdornment={true}
                adornment="%"
                error={totalPercent !== 100}
              ></Item>
              <Item value={splitPx} onChange={onSplitPriceChanges(i)} label={`Price-${i + 1}`} adornment="£"></Item>
              <Item
                value={currentValue}
                onChange={onCurrentSplitValueChanges(i)}
                label={`Current Value-${i + 1}`}
                adornment="£"
              ></Item>
              <Item
                value={currentPercent}
                endAdornment={true}
                disabled
                label={`Current%-${i + 1}`}
                adornment="%"
              ></Item>
              <Item value={rebalanceAmt.toFixed(2)} disabled label={`Rebalance-${i + 1}`} adornment="£"></Item>
            </Box>
          );
        })}
      </Box>

      <IconButton color="primary" aria-label="add to shopping cart" onClick={handleAddtargetSplits}>
        <Icon sx={{ color: green[500] }}>add_circle</Icon>
      </IconButton>
      <IconButton color="secondary" onClick={() => handleRemovetargetSplits(targetSplits.length - 1)}>
        <Icon fontSize="small" sx={{ color: red[500] }}>
          remove_circle
        </Icon>
      </IconButton>
      <Box>
        <Paper style={{ paddingTop: 15, paddingBottom: 15, marginBottom: 15 }}>
          <Typography variant="subtitle2">Rebalance Summary</Typography>
          {rebelanceSummary.map(({ side, rebalanceQty, txTotal }, i) => {
            return (
              <>
                <Typography variant="body1" component="h2" key={i}>
                  #{i + 1} {side} {rebalanceQty} items for £{txTotal}
                </Typography>
              </>
            );
          })}
          <Typography variant="subtitle1">Balance in hand after rebalance: £{totalRebalanaceResidue}</Typography>
        </Paper>
      </Box>
    </div>
  );
}
